html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, dl, dt, dd, ol, ul, li, fieldset, figure, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-weight: inherit;
  font-style: inherit;
  font-family: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

body {
  line-height: 1;
  color: #000;
  background: #fff;
}

ol, ul {
  list-style: none;
}

table {
  border-collapse: separate;
  border-spacing: 0;
  vertical-align: middle;
}

caption, th, td {
  text-align: left;
  font-weight: normal;
  vertical-align: middle;
}

a img {
  border: 0;
}

.hidden {
  display: none;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

html {
  box-sizing: border-box;
  -webkit-text-size-adjust: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Proxima-Nova", Helvetica, Arial, sans-serif;
  line-height: 1;
  color: #000;
  background: #fff;
  margin: -1px 0 0;
}

button,
input,
optgroup,
select,
textarea {
  font: inherit;
}

input,
button,
textarea {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  border-radius: 0;
}

strong {
  font-weight: bold;
}

hr {
  border: 0;
  border-top: 1px solid #DADAE7;
  width: 100%;
  margin: 2em 0;
}

ul {
  margin: 0;
  padding: 0;
}

[class*="heading"] {
  margin-bottom: 0.5em;
}

.c-heading-1 {
  font-size: 40px;
  line-height: 1;
  font-weight: 600;
}

.c-heading-2 {
  font-size: 28px;
  color: #7F7E9E;
  line-height: 1;
  font-weight: 300;
  line-height: 1.2em;
}

@media screen and (min-width: 768px) {
  .c-heading-2 {
    font-size: 42px;
  }
}

.c-heading-4 {
  font-size: 30px;
  color: #7F7E9E;
  font-weight: 300;
}

.c-heading-5 {
  font-size: 26px;
  color: #7F7E9E;
  font-weight: 300;
}

.c-subheading-1 {
  font-size: 20px;
  font-weight: 300;
  line-height: 1.5em;
}

.c-subheading-5 {
  font-size: 18px;
  font-weight: 300;
}

[class*="c-paragraph"] {
  margin-bottom: 1em;
}

.c-paragraph-0 {
  font-size: 0.9rem;
}

.c-paragraph-1 {
  font-size: 1rem;
}

.c-paragraph-2 {
  font-size: 20px;
  font-weight: 300;
}

.button {
  border: 0;
  padding: 0;
  margin: 0;
  line-height: 2.65;
  display: inline-block;
  text-align: center;
  border-radius: 6px;
  color: #fff;
  background-color: #F2778A;
  min-width: 80%;
}

@media screen and (min-width: 768px) {
  .button {
    min-width: 260px;
  }
}

.button--cta {
  color: rgba(0, 0, 0, 0.4);
  font-weight: bold;
  background-color: #FFC442;
  line-height: 3.75;
  font-size: 1em;
}

.c-price {
  font-size: 22px;
  color: #989898;
  margin-bottom: 0.3em;
}

.c-price + .c-caption strong {
  color: black;
}

.c-price [itemprop="price"] {
  color: black;
  font-size: 30px;
  font-weight: bold;
}

.c-price ~ .button {
  width: 0;
  margin: 1.5em auto;
}

@media screen and (min-width: 768px) {
  .c-price ~ .button {
    margin: 1.5em auto;
    min-width: 250px !important;
  }
}

.c-caption {
  color: #989898;
  font-size: 1em;
}

.c-link {
  color: #67A2EE;
  font-weight: bold;
}

