.slider {
  display: inline-block;
  vertical-align: middle;
  position: relative;
}

.slider.slider-horizontal {
  width: 210px;
  height: 20px;
}

.slider.slider-horizontal .slider-track {
  height: 10px;
  width: 100%;
  margin-top: -16px;
  top: 50%;
  left: 0;
}

.slider.slider-horizontal .slider-selection {
  height: 100%;
  top: 0;
  bottom: 0;
}

.slider.slider-horizontal .slider-handle {
  margin-left: -10px;
  margin-top: -5px;
}

.slider.slider-horizontal .slider-handle.triangle {
  border-width: 0 10px 10px 10px;
  width: 0;
  height: 0;
  border-bottom-color: #0480be;
  margin-top: 0;
}

.slider.slider-vertical {
  height: 210px;
  width: 20px;
}

.slider.slider-vertical .slider-track {
  width: 10px;
  height: 100%;
  margin-left: -5px;
  left: 50%;
  top: 0;
}

.slider.slider-vertical .slider-selection {
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
}

.slider.slider-vertical .slider-handle {
  margin-left: -5px;
  margin-top: -10px;
}

.slider.slider-vertical .slider-handle.triangle {
  border-width: 10px 0 10px 10px;
  width: 1px;
  height: 1px;
  border-left-color: #0480be;
  margin-left: 0;
}

.slider input {
  display: none;
}

.slider .tooltip-inner {
  white-space: nowrap;
  width: 60px;
  height: 30px;
  font-size: 18px;
}

.slider-track {
  position: absolute;
  cursor: pointer;
  background-color: #fff;
  background-repeat: repeat-x;
  border-radius: 4px;
}

.slider-selection {
  position: absolute;
  background-color: #A8CEFF;
  background-repeat: repeat-x;
  box-sizing: border-box;
  border-radius: 4px;
}

.slider-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: #67A2EE;
  background-repeat: repeat-x;
  border: 0px solid transparent;
}

.slider-handle.round {
  border-radius: 20px;
}

.slider-handle.triangle {
  background: transparent none;
}

.tooltip.top {
  opacity: 1 !important;
}

[class="slider slider-horizontal"] {
  width: 250px !important;
}

@media screen and (min-width: 768px) {
  [class="slider slider-horizontal"] {
    width: 400px !important;
  }
}

@media screen and (min-width: 1280px) {
  [class="slider slider-horizontal"] {
    width: 600px !important;
  }
}

.c-slider {
  margin: 2.5em auto 1em;
}

.c-increment {
  text-align: right;
  display: table-cell;
}

.c-increment [type="text"] {
  width: 40px;
  text-align: left;
  padding: 0 10px;
  height: 42px;
  border: 1px solid #DFDFE1;
  border-radius: 4px;
  margin: 0 8px;
}

@media screen and (min-width: 375px) {
  .c-increment [type="text"] {
    width: 60px;
  }
}

@media screen and (min-width: 1280px) {
  .c-increment [type="text"] {
    width: 80px;
  }
}

.c-increment__button:hover {
  cursor: pointer;
}

.c-increment__button svg {
  position: relative;
  top: 5px;
  width: 22px;
  height: 22px;
}

.c-align-list__line-item {
  display: table;
  margin-left: auto;
  width: 100%;
  margin-bottom: 2em;
}

@media screen and (min-width: 768px) {
  .c-align-list__line-item {
    margin-bottom: 1em;
  }
}

.c-align-list__string {
  color: #989898;
  width: 40%;
  text-align: left;
  display: table-cell;
  vertical-align: top;
}

@media screen and (min-width: 375px) {
  .c-align-list__string {
    width: 45%;
  }
}

@media screen and (min-width: 320px) and (orientation: landscape) {
  .c-align-list__string {
    text-align: right;
    width: 75%;
    vertical-align: middle;
  }
}

@media screen and (min-width: 768px) {
  .c-align-list__string {
    text-align: right;
    width: 70%;
    vertical-align: middle;
  }
}

@media screen and (min-width: 1280px) {
  .c-align-list__string {
    width: 73%;
    width: 440px;
  }
}

.Homepage--mobile-cta {
  display: none;
  width: 100%;
  font-size: 16.6px;
  color: #FFFFFF;
  background: #FFC442;
  text-align: center;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-width: 1px 0;
  text-decoration: none;
  padding: 21px 0 16px;
}

.Homepage--mobile-cta:active,
.Homepage--mobile-cta:hover,
.Homepage--mobile-cta:visited {
  color: #FFFFFF;
  text-decoration: none;
}

.Homepage--mobile-cta:active,
.Homepage--mobile-cta:hover {
  background: #FEBE30;
}

@media (max-width: 767px) {
  .Homepage--mobile-cta {
    display: block;
  }
}

.Homepage-Header .item,
.Homepage-Header .social,
.Homepage-Header .spacer,
.Homepage-Header .logo,
.Homepage-Header .burger,
.Homepage-Header .signup {
