.m-hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  text-align: center;
  min-height: 484px;
  padding: 0 2rem;
}

.m-hero--pricing {
  background: #7F7E9E;
  background-image: linear-gradient(-180deg, rgba(242, 119, 137, 0) 1%, rgba(255, 255, 255, 0.5) 99%);
}

.m-hero--pricing .button--cta {
  margin-top: 2.5em;
}

.m-feature {
  width: 100%;
  border: 1px solid #E0E0E0;
  border-radius: 6px;
  background-color: rgba(127, 126, 158, 0.04);
}

@media screen and (min-width: 1024px) {
  .m-feature {
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .m-feature {
    width: 1176px;
    margin: 0 auto;
  }
}

.m-feature [class*="c-heading"] {
  margin-bottom: 0.2em;
}

.m-feature .button {
